<template>
    <div class="main-page">
        <div class="headerTag">知识中心</div>
        <ScrollView ref="ScrollView" top="40px" bottom="0" @down="down" @upFun="up">
            <list-line v-for="(item, index) in dataList" :key="index" :item="item" :diynew=true :defaultProp="defaultProp" toPageUrl="/modular/oa/DocumentDetails" />
        </ScrollView>
    </div>
</template>
			
<script>
import ListLine from "@/components/ListLine.vue";
export default {
    components: { ListLine },
    data() {
        return {
            name: "7a956b2c-5615-4486-991b-9329bb29f406",
            active: 2,
            dataList: [],
            searchInfo: {},
            defaultProp: {
                img: "FIcon",
                title: "FName"
            }
        };
    },
    methods: {
        change(name, title) {
            this.name = name;
            this._api
                .OA_DC_DocumentsIndex({
                    page: 1,
                    pageSize: 20,
                    ClassId: name,
                })
                .then((res) => {
                    this.dataList = res.data;
                });
        },

        down(instance) {
            this.$refs["ScrollView"].reUp();
        },
        // 上拉加载
        up([page, instance]) {
            this._api
                .OA_DC_DocumentsIndex({
                    ...this.searchInfo,
                    page: page.num,
                    pageSize: page.size,
                    ClassId: this.name,
                })
                .then((res) => {
                    if (res) {
                        let arr = res.data;
                        if (page.num === 1) this.dataList = [];
                        this.dataList = this.dataList.concat(arr);
                        this.$nextTick(() => {
                            instance.endSuccess(arr.length);
                        });
                    }
                    this.loading = false;
                });
        }
    },
    mounted() { },
};
</script>
			
<style lang="scss" scoped>
.main-page {
    .listcard {
        width: calc(100% - 30px);
        display: flex;
        justify-content: space-between;
        height: 208rpx;
        padding: 9px;
        margin: 10px 15px;
        .show {
            width: 100px;
            height: 80px;

            .showImg {
                width: 100%;
                height: 100%;
                border-radius: 4px;
            }
        }
        .right {
            width: 65%;
            margin-left: 30rpx;
            line-height: 30rpx;

            .title {
                line-height: 52rpx;
                font-size: 17px;
                color: #303133;
                font-weight: 600;
                margin-bottom: 6px;

                text {
                    font-weight: 600;
                    font-size: 32rpx;
                }
            }

            .time-wrap {
                display: flex;
                line-height: 30px;
                color: #666;
                font-size: 15px;

                i {
                    margin-right: 6px;
                    font-size: 14px;
                }
            }
        }
    }
}
</style>
